body, html {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f5f5f5;
    font-family: Arial, sans-serif;
}

.centered-div {
    background-color: #ffffff;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: center;
    min-width: 500px;
    margin: 10px;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 10px 0;
    color: #333;
}

h1 {
    font-size: 2.5em;
    font-weight: 700;
    color: #2c3e50;
    border-bottom: 2px solid #2c3e50;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

h2 {
    font-size: 2em;
    font-weight: 600;
    color: #34495e;
    padding-bottom: 8px;
    margin-bottom: 18px;
}

h3 {
    font-size: 1.75em;
    font-weight: 500;
    color: #5d6767;
    padding-bottom: 6px;
    margin-bottom: 16px;
}

h4 {
    font-size: 1.5em;
    font-weight: 500;
    color: #95a5a6;
    margin-bottom: 14px;
}

h5 {
    font-size: 1.25em;
    font-weight: 400;
    color: #bdc3c7;
    margin-bottom: 12px;
}

h6 {
    font-size: 1em;
    font-weight: 400;
    color: #ecf0f1;
    margin-bottom: 10px;
}

hr {
    border: none;
    border-radius: 3px;
    height: 3px;
    background: linear-gradient(to right, #007bff, #00c851);
    margin: 20px 0;
    width: auto;
}

hr.dashed {
    border: none;
    height: 1px;
    background: none;
    border-top: 2px dashed #ccc;
    width: 80%;
    margin: 20px 0;
}

.err {
    color: indianred;
}

.done {
    color: forestgreen;
}

.errInput {
    border: 3px solid indianred;
}

.main-content {
    padding-top: 60px;
}

.spaceBetween {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

span {
    font-family: Arial, sans-serif;
    font-size: 24px;
    color: darkslateblue;
}

span.gradient {
    background: linear-gradient(to right, #5fe2ff, #a77bfe);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

span.text-shadow {
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

span.clickable {
    cursor: pointer;
}

span.hover-animate:hover {
    color: #ff6347;
    transition: color 0.3s ease-in-out;
}

.twoButtonsModal {
    margin: 0 65px 0 65px;
}

[data-tooltip] {
    position: relative; /* Относительное позиционирование */
}
[data-tooltip]::after {
    content: attr(data-tooltip); /* Выводим текст */
    position: absolute; /* Абсолютное позиционирование */
    width: 300px; /* Ширина подсказки */
    left: 0; top: 0; /* Положение подсказки */
    background: #a77bfe; /* Синий цвет фона */
    border-radius: 5px;
    color: #fff; /* Цвет текста */
    font-size: 20px;
    text-align: center;
    padding: 0.7em; /* Поля вокруг текста */
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5); /* Параметры тени */
    pointer-events: none; /* Подсказка */
    opacity: 0; /* Подсказка невидима */
    transition: 0.75s; /* Время появления подсказки */
}
[data-tooltip]:hover::after {
    opacity: 1; /* Показываем подсказку */
    top: 2em; /* Положение подсказки */
}